.testimonial{
    height: auto;
}
.content-card{
    box-shadow: 5px 5px 7px -5px #cd5c5c;
    padding: 20px;
    margin-top: 50px;
    color: black;
}
.content-card:hover{
    box-shadow: 7px 7px 14px 1px #cd5c5c;
    background-color: #8a2929;
    color: white;
}
.content-card img{
    height: 70px;
    width: 70px;
    border-radius: 90px;
    object-fit: cover;
    margin-top: -30px;
    border: 3px solid rgb(185, 62, 40);
}
.content-card img:hover{
    border: 3px solid white;
}
.name-style{
    color: #d46565;
}