.nav-main{
    background-color: #cd5c5c;
    width: 100%;
    height: 70px;
    display: flex;
    align-items: center;
    top: 0;
    text-align: center;
    justify-content: space-between;
}
.nav-main ul{
    display: flex;
    margin-right: 7%;
}
.nav-main ul li{
    list-style: none;
    margin-right: 30px;
    color: white;
    cursor: pointer;
    padding-top: 12px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
.nav-main ul li:hover{
    color: yellow;
}
.img-style{
    margin-left: 3%;
}